import React, { Dispatch, SetStateAction } from 'react';
import { Snackbar, TextField, Button, Grid, Typography } from '@material-ui/core';
import styles from '../styling.module.scss';
import RetimingButtons from './RetimingButtons';

export interface RetimerBaseProps {
  getVideo: (event: any) => void,
  redirect: () => void,
  stepBy: (frames: number) => void,
  setStartTime: Dispatch<SetStateAction<number>>,
  setEndTime: Dispatch<SetStateAction<number>>,
  url: string,
  videoLoaded: boolean,
  copied: boolean,
  startTime: number,
  endTime: number,
}

const RetimerBase = (props: RetimerBaseProps) => {
  const {
    getVideo,
    redirect,
    stepBy,
    url,
    videoLoaded,
    copied,
    startTime,
    endTime,
  } = props;
  return (
    <Grid container justify="center">
      <Grid container item xs={10} justify="center">
        <Grid container item xs={12} justify="center">
          <div className={styles.basicMargin}>
            <Typography variant="h4">Speedrun Retimer</Typography>
          </div>
        </Grid>
        <Grid container item xs={12} justify="center">
          <div className={styles.videoURLWidth}>
            <TextField
              label="Video URL"
              value={url}
              id="url"
              onChange={(event) => getVideo(event)}
              fullWidth
            />
          </div>
        </Grid>
        <Grid container item xs={12} justify="center">
          <div className={styles.basicMargin}>
            <Button
              color="primary"
              variant="contained"
              onClick={() => redirect()}
            >
              Load Video
            </Button>
          </div>
        </Grid>
        <Grid container item xs={12} justify="center">
          <div id="video-div" className={styles.basicMargin} />
        </Grid>
        <Grid container item xs={12} justify="center">
          <div className={styles.basicMargin}>
            <Typography variant="h6">Step Forwards or Backwards:</Typography>
          </div>
        </Grid>
        <Grid container item xs={12} justify="center">
          <div className={styles.basicMargin}>
            <RetimingButtons stepBy={stepBy} />
          </div>
        </Grid>
        <Grid container item xs={12} justify="center">
          <div className={styles.basicMargin}>
            <Typography id="current-time">Current time: 0 ms</Typography>
            <Typography id="current-time">Current frame: 0</Typography>
            <TextField
              label="Start Time"
              value={startTime}
              id="start-time"
              fullWidth
            />
            <TextField
              label="End Time"
              value={endTime}
              id="end-time"
              fullWidth
            />
          </div>
        </Grid>
        <Snackbar
          open={copied}
          message="Copied! Please paste the mod message into the comment of the run you are verifying."
        />
      </Grid>
    </Grid>
  );
}

