import React, { useState } from 'react';
import RetimerBase from './RetimerBase';

const RetimerBaseContainer = () => {

  const [url, setURL] = useState("");
  const [videoLoaded, setVideoLoaded] = useState(false);
  const [copied, setCopied] = useState(false);
  const [startTime, setStartTime] = useState(0);
  const [endTime, setEndTime] = useState(0);

  function parseYoutubeId(videoUrl: string) {
    var reg1 = videoUrl.match(/youtube\..+?\/watch.*?v=(.*?)(?:&|\/|$)/);
    if (reg1 && reg1.length >= 2) {
      return reg1[1];
    }

    var reg2 = videoUrl.match(/youtu\.be\/(.*?)(?:\?|&|\/|$)/);
    if (reg2 && reg2.length >= 2) {
      return reg2[1];
    }

    var reg3 = videoUrl.match(/youtube\..+?\/embed\/(.*?)(?:\?|&|\/|$)/);
    if (reg3 && reg3.length >= 2) {
      return reg3[1];
    }

    return videoUrl;
  }

  const getVideo = (event: any) => {
    setURL(event.target.value);
  }

  const redirect = () => {
    window.location.href = "/speedrun-retimer?id=" + parseYoutubeId(url);
  }

  const stepBy = (frames: number) => {
    setCopied(true);
  }

  return (
    <RetimerBase
      getVideo={getVideo}
      redirect={redirect}
      stepBy={stepBy}
      setStartTime={setStartTime}
      setEndTime={setEndTime}
      url={url}
      videoLoaded={videoLoaded}
      copied={copied}
      startTime={startTime}
      endTime={endTime}
    />
  )
}

export default RetimerBaseContainer;